.card {
  position: relative;
}

.card__media img {
  width: 100%;
}

.card__body {
  padding-top: 20px;
}

.card__description {
  margin-top: 15px;
}

.card__description:first-child {
  margin-top: 0;
}

.card__description:last-child {
  margin-bottom: 0;
}

.card__footer {
  padding-top: 20px;
}

.card__link {
  text-indent: 200%;
  white-space: nowrap;
  position: absolute;
  inset: 0;
  overflow: hidden;
}

